<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">用户管理</el-row>
    <el-row type="flex" justify="start" style="margin-top: 10px;">
      <el-button plain @click="exportUserDate">导出用户数据<i class="el-icon-download el-icon--right"></i></el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="userList" style="width: 100%">
        <el-table-column label="图像" >
          <template slot-scope="scope">
            <img :src="scope.row.avatarUrl" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="nickName" label="微信名" />
        <el-table-column prop="openid" label="open Id" show-overflow-tooltip />
        <el-table-column prop="public_at" label="手机号" show-overflow-tooltip />
        <el-table-column prop="public_at" label="地址" show-overflow-tooltip />
        <el-table-column label="我的总关注数" show-overflow-tooltip />
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total" />
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      userList: [],
      curPage: 1,
      pageSize: 20,
      total: 100
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.userList(params).then((res) => {
        console.log('res', res)

        if (res.data.retcode == 0) {
          let userList = res.data.result_rows.users
          userList.forEach(item => {
            item.avatarUrl = item.raw.avatarUrl,
            item.nickName = item.raw.nickName
          });
          console.log('userList', userList)
          this.userList = userList
          this.total = res.data.result_rows.page.total_count
        }
      })
    },

    exportUserDate () {
      this.$http.exportUsers().then((res) => {

      })
    },
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
      this.queryList()
    },
    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
      this.queryList()
    }
  },
  created () {
    this.queryList()
  }
}
</script>

